// app/javascript/components/Library/index.js
import React from 'react';
import { Query } from 'react-apollo';
import gql from 'graphql-tag';
import AddItemForm from '../AddLeague';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const LeagueQuery = gql`
  {
    allLeagues {
      id
      name
      lonlat
      price
    }
  }
`;

export default () => (
  <Query query={LeagueQuery}>
    {({ data, loading }) => (
      <TableContainer
        style={{ margin: 'auto', width: '60%', paddingTop: '15px' }}
        component={Paper}
      >
        <Table size="small">
          <TableHead>
            <TableRow>
              <TableCell>League</TableCell>
              <TableCell align="right">Location</TableCell>
              <TableCell align="right">Price</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {loading ? (
              <TableRow key="empty">
                <TableCell component="th" scope="row">
                  There are currently no leagues available.
                </TableCell>
              </TableRow>
            ) : (
              data.allLeagues.map(({ id, name, lonlat, price }) => (
                <TableRow key={id}>
                  <TableCell component="th" scope="row">
                    {name}
                  </TableCell>
                  <TableCell align="right">
                    {lonlat.match(/\([^\)]+\)/g)[0].replace(' ', ', ')}
                  </TableCell>
                  <TableCell align="right">${price}</TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
        <AddItemForm />
      </TableContainer>
    )}
  </Query>
);
